
import moment from 'moment'
import util from '@/libs/util'
import {by} from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Card from 'vux/src/components/card/index.vue'

import Progress from 'c%/progress'

export default{
mixins: [common,globals],
components: {
Card,
Progress
},
data () {
return {
data: [],
params: {},
noDataText: '您今天还没有排课~'
}
},
mounted () {

},
computed: {
min_before_class () {
let result = 15
if(this.params&&this.params.class_attendance){
result = this.params.class_attendance.min_before_class
}
return result
},
min_after_class () {
let result = 15
if(this.params&&this.params.class_attendance){
result = this.params.class_attendance.min_after_class
}
return result
},
data_uri () {
let uri = util.sprintf('course_arranges?with=class_attendance&int_day=%s&teach_eid=%s',moment(new Date()).format("YYYYMMDD"),this.eid$)
return uri
}
},
methods: {
redirect (row) {
this.$router.push({path:'./schedule/'+row.ca_id})
},
loadFinished () {
this.getParams()
},

getParams () {
this.$rest('configs?cfg_name=params').get()
.success(data=>{
this.params = data.cfg_value
this.getNearestArrange()
})
},
getTeachPercent (row) {
return (row.attendance_times / row.arrange_times) * 100
},
getNearestArrange () {
let nowday = moment(new Date()).format('YYYYMMDD')
let nowhour = moment(new Date()).format('HHmm')
let data = this.data.sort(by('int_start_hour',1))

if(data.length>0){
data.forEach((d,i)=>{
let diff1 = this.getMinDiff(d.int_day,d.int_start_hour)
let diff2 = this.getMinDiff(d.int_day,d.int_end_hour)

if(diff1 < this.min_before_class||diff2 < this.min_after_class){
data.splice(i,1)
this.$set(d,'_active',true)
this.data = data.slice(0,0).concat(d, data.slice(0))
}

})
}
},
getMinDiff (day,hour) {
let min1 =  moment(new Date()).format('YYYY-MM-DD HH:mm')
let min2 = this.$filter('int_date')(day)+ ' ' +this.$filter('int_hour')(hour)
let diff = Math.abs(moment(min1).diff(min2))/(1000*60)
return diff
}
}
}



